<script>
let timer;
export default {
  name: "",
  data() {
    return {
      showBj: false
    };
  },

  components: {},

  methods: {
    toBj() {
      this.showBj = !this.showBj;
      if (this.showBj) {
        let keyword = document.getElementById("keyword");
        let keywordBox = document.getElementById("keywordBox");
        console.log(keyword.offsetTop, keywordBox.scrollTop);
        timer && clearInterval(timer);
        if (keywordBox.scrollTop > keyword.offsetTop) {
          timer = setInterval(() => {
            keywordBox.scrollTop = keywordBox.scrollTop - 20;
            if (keywordBox.scrollTop <= keyword.offsetTop - 120) {
              timer && clearInterval(timer);
            }
          }, 100);
        } else {
          timer = setInterval(() => {
            keywordBox.scrollTop = keywordBox.scrollTop + 20;
            if (keywordBox.scrollTop >= keyword.offsetTop - 140) {
              timer && clearInterval(timer);
            }
          }, 100);
        }
      }
    },
    toNav(url) {
      this.$router.push({
        path: url
      });
    }
  }
};
</script>

